import {
    ListItem,
    ListItemText,
    ListItemSecondaryAction,
    IconButton,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";

const Item = ({ itemkey, name, description, link, price, click, auth }) => {
    return (
        <ListItem button onClick={() => window.open(link, "_blank")}>
            <ListItemText
                id={itemkey}
                primary={`${name} ${description} ${price}`}
            />
            <ListItemSecondaryAction>
                <IconButton edge="end">
                    <ShoppingCartIcon />
                </IconButton>
                {auth && (
                    <>
                        <IconButton
                            edge="end"
                            onClick={() => click(itemkey, auth)}
                        >
                            <DeleteIcon />
                        </IconButton>
                    </>
                )}
            </ListItemSecondaryAction>
        </ListItem>
    );
};

export default Item;
